
import CheckIcon from 'vux/src/components/check-icon/index.vue'

import api from '../../assets/js/api'
import Nodata  from '../../components/nodata.vue'

export default {
components: {
CheckIcon,
Nodata
},
data () {
return {
demo1: false,
bottomCount: 2,
isEdit: false,
list: [
{
"name": "张三",
"phone": 12323,
"address": "广东省广州市",
"type": "default",
"address_id": 1
},
{
"name": "张三",
"phone": 12323,
"address": "北京省天津市",
"type": "normal",
"address_id": 2
},
]
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(text){
this.$vux.loading.show({
text: text || '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
deleteAddress(){
const _this = this;
this.$vux.confirm.show({

content: '删除后不可恢复，确认删除？',

onCancel () {
console.log(this) 
console.log(_this) 
},
onConfirm () {
}
})
},
setMainAddress(){
const _this = this;
this.$vux.confirm.show({

content: '确定设置为默认地址？',

onCancel () {
console.log(this) 
console.log(_this) 
},
onConfirm () {
}
})
},
gotoEditAddress(id){
this.$router.push({
path: `/addAddress?type=edit&id=${id}`
})
},
gotoAddAddress(){
this.$router.push({
path: '/addAddress?type=add'
})
},

getAddressList(){
this.showLoading('正在获取')
this.$http.post(api.addressManage)
.then(res => {
this.hideLoading();
this.list = res.data;
}).catch(e => {
this.hideLoading();
})
}
},
mounted(){
this.getAddressList();

document.getElementsByTagName('title')[0].textContent = '收货地址管理';
}
}



