
import XImg from 'vux/src/components/x-img/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'

export default {
directives: {
TransferDom
},
components: {
XImg,
CheckIcon,
Popup
},
data () {
return {
demo1: true,
bottomCount: 2,
isPost: true,
showChoice: false,
hasAddress: true
}
},
methods: {
setRecvier(){
if (this.isPost) {
this.$router.push({
path: '/address'
})
} else {
this.$router.push({
path: '/selfGet'
})
}
},
log(start){
console.log(start)
},
gotoOnlinePay(){
if (this.isPost) {
this.$router.push({
path: `/postSuc?order_id=234234`
})
} else {
this.$router.push({
path: `/selfGetSuc?order_id=234234`
})
}
},
gotoOffinePay(){
this.$router.push({
path: '/offinePay'
})
}
}
}



